// 스토어 헤더 스타일 완전 복제
.user-detail-dialog {
  width: 700px;
  max-width: 90vw;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  border-radius: 0;
  overflow: hidden;

  // 헤더 - top-menu-bar 스타일 정확히 복제
  .dialog-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 56px;
    padding: 0 20px;
    background: linear-gradient(135deg, #ff6b35 0%, #e55a2b 100%);
    color: white;

    .header-left {
      display: flex;
      align-items: center;
      gap: 8px;
      flex: 1;
      min-width: 0;
      overflow: hidden;

      .user-avatar {
        width: 32px;
        height: 32px;
        border-radius: 50%;
        background: rgba(255, 255, 255, 0.2);
        color: white;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;

        mat-icon {
          font-size: 24px;
          width: 24px;
          height: 24px;
        }
      }

      .user-details {
        display: flex;
        flex-direction: column;
        gap: 2px;
        min-width: 0;
        flex: 1;

        .user-name {
          font-size: 13px;
          font-weight: 600;
          color: white;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          max-width: 200px;
          line-height: 1.2;
          display: flex;
          align-items: center;
          gap: 4px;

          .me-badge {
            font-size: 16px;
            width: 16px;
            height: 16px;
            color: #ffd700;
            flex-shrink: 0;
          }
        }

        .user-badges {
          display: flex;
          gap: 4px;
          flex-wrap: wrap;

          .role-badge,
          .status-badge {
            font-size: 10px;
            color: rgba(255, 255, 255, 0.85);
            background: rgba(255, 255, 255, 0.15);
            padding: 2px 8px;
            border-radius: 10px;
            white-space: nowrap;
            font-weight: 500;
          }

          .role-badge {
            &.role-customer {
              background: rgba(33, 150, 243, 0.25);
            }
            &.role-seller {
              background: rgba(255, 152, 0, 0.25);
            }
            &.role-admin {
              background: rgba(233, 30, 99, 0.25);
            }
            &.role-super-admin {
              background: rgba(156, 39, 176, 0.25);
            }
          }

          .status-badge {
            &.status-active {
              background: rgba(76, 175, 80, 0.25);
            }
            &.status-inactive {
              background: rgba(244, 67, 54, 0.25);
            }
            &.status-suspended {
              background: rgba(255, 152, 0, 0.25);
            }
            &.status-deleted {
              background: rgba(158, 158, 158, 0.25);
            }
          }
        }
      }
    }

    .header-right {
      display: flex;
      align-items: center;
      gap: 12px;

      .close-btn {
        background: none;
        border: none;
        color: white;
        cursor: pointer;
        padding: 8px;
        border-radius: 4px;
        transition: background-color 0.2s;
        flex-shrink: 0;
        
        &:hover {
          background: rgba(255, 255, 255, 0.1);
        }

        mat-icon {
          font-size: 24px;
          width: 24px;
          height: 24px;
        }
      }
    }
  }

  // 본문
  .dialog-body {
    flex: 1;
    overflow-y: auto;
    padding: 20px;
    background: var(--bg-primary);

    .info-section {
      background: var(--bg-secondary);
      border-radius: 8px;
      padding: 16px;
      margin-bottom: 12px;
      border: 1px solid var(--border-color);
      transition: all 0.2s ease;

      &:last-child {
        margin-bottom: 0;
      }

      .section-title {
        display: flex;
        align-items: center;
        gap: 8px;
        margin: 0 0 12px 0;
        padding-bottom: 8px;
        font-size: 13px;
        font-weight: 600;
        color: var(--text-primary);
        border-bottom: 1px solid var(--border-color);

        mat-icon {
          font-size: 18px;
          width: 18px;
          height: 18px;
          opacity: 0.7;
        }
      }

      .info-grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 12px;

        @media (max-width: 600px) {
          grid-template-columns: 1fr;
        }

        .info-item {
          display: flex;
          flex-direction: column;
          gap: 4px;

          &.full-width {
            grid-column: 1 / -1;
          }

          label {
            font-size: 11px;
            font-weight: 500;
            color: var(--text-secondary);
            text-transform: uppercase;
            letter-spacing: 0.5px;
            opacity: 0.8;
          }

          .info-value {
            display: flex;
            align-items: center;
            gap: 6px;
            font-size: 13px;
            color: var(--text-primary);
            font-weight: 500;
            word-break: break-all;

            .info-icon {
              font-size: 16px;
              width: 16px;
              height: 16px;
              color: var(--text-secondary);
              opacity: 0.6;
            }

            .role-id {
              font-size: 11px;
              color: var(--text-secondary);
              opacity: 0.7;
              font-weight: normal;
            }
          }

          .permissions-list {
            display: flex;
            flex-wrap: wrap;
            gap: 4px;

            .permission-chip {
              display: inline-flex;
              align-items: center;
              padding: 3px 8px;
              border-radius: 10px;
              background: rgba(255, 255, 255, 0.05);
              color: var(--text-primary);
              font-size: 10px;
              font-weight: 500;
              border: 1px solid var(--border-color);
            }
          }
        }
      }

      // 통계 섹션
      &.stats-section {
        .stats-grid {
          display: grid;
          grid-template-columns: repeat(3, 1fr);
          gap: 8px;

          @media (max-width: 600px) {
            grid-template-columns: 1fr;
          }

          .stat-card {
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 12px;
            border-radius: 6px;
            background: var(--bg-primary);
            text-align: center;
            gap: 6px;
            border: 1px solid var(--border-color);
            transition: all 0.2s ease;

            &:hover {
              border-color: var(--accent-primary);
              background: var(--bg-secondary);
            }

            mat-icon {
              font-size: 24px;
              width: 24px;
              height: 24px;
              opacity: 0.7;
              color: var(--accent-primary);
            }

            .stat-value {
              font-size: 16px;
              font-weight: 700;
              color: var(--text-primary);
            }

            .stat-label {
              font-size: 10px;
              color: var(--text-secondary);
              font-weight: 500;
              opacity: 0.8;
            }
          }
        }
      }
    }
  }

  // 푸터
  .dialog-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 20px;
    border-top: 1px solid var(--border-color);
    background: var(--bg-secondary);

    .footer-left,
    .footer-right {
      display: flex;
      gap: 8px;
      flex-wrap: wrap;
    }

    button {
      padding: 6px 12px;
      border-radius: 6px;
      font-weight: 500;
      font-size: 12px;
      transition: all 0.2s ease;
      border: 1px solid transparent;
      cursor: pointer;
      display: flex;
      align-items: center;
      gap: 4px;

      mat-icon {
        font-size: 14px;
        width: 14px;
        height: 14px;
      }

      &.close-btn {
        background: var(--bg-primary);
        color: var(--text-primary);
        border-color: var(--border-color);

        &:hover {
          background: var(--bg-tertiary);
          transform: translateY(-1px);
        }
      }

      &.edit-btn {
        background: var(--gradient-primary);
        color: white;
        border: none;

        &:hover {
          transform: translateY(-1px);
          box-shadow: 0 2px 8px var(--shadow);
        }
      }

      &.status-btn {
        background: var(--success-color);
        color: white;
        border: none;

        &.deactivate {
          background: var(--warning-color);
        }

        &:hover {
          transform: translateY(-1px);
          box-shadow: 0 2px 8px var(--shadow);
        }
      }

      &.delete-btn {
        background: var(--error-color);
        color: white;
        border: none;

        &:hover {
          transform: translateY(-1px);
          box-shadow: 0 2px 8px rgba(244, 67, 54, 0.3);
        }
      }
    }
  }
}

// 다크 테마
.dark-theme {
  .user-detail-dialog {
    .dialog-header {
      .header-left {
        .user-avatar {
          background: rgba(255, 255, 255, 0.15);
        }
      }
    }

    .dialog-body {
      .info-section {
        background: var(--bg-secondary);

        .info-grid .info-item {
          .permissions-list .permission-chip {
            background: rgba(255, 255, 255, 0.03);
            border-color: var(--border-color);
          }
        }

        &.stats-section .stats-grid .stat-card {
          background: var(--bg-primary);
        }
      }
    }

    .dialog-footer {
      background: var(--bg-secondary);
      
      button.close-btn {
        background: var(--bg-tertiary);
      }
    }
  }
}

// 반응형
@media (max-width: 768px) {
  .user-detail-dialog {
    max-width: 95vw;

    .dialog-header {
      height: auto;
      min-height: 56px;
      padding: 12px 16px;
      flex-wrap: wrap;

      .header-left {
        gap: 8px;

        .user-avatar {
          width: 28px;
          height: 28px;

          mat-icon {
            font-size: 20px;
            width: 20px;
            height: 20px;
          }
        }

        .user-details {
          .user-name {
            font-size: 12px;
            max-width: 150px;
          }
        }
      }
    }

    .dialog-body {
      padding: 16px;

      .info-section {
        padding: 12px;

        .section-title {
          font-size: 12px;
        }

        .info-grid {
          gap: 10px;

          .info-item {
            label {
              font-size: 10px;
            }

            .info-value {
              font-size: 12px;
            }
          }
        }
      }
    }

    .dialog-footer {
      flex-direction: column;
      gap: 8px;
      padding: 12px 16px;

      .footer-left,
      .footer-right {
        width: 100%;
        justify-content: center;

        button {
          flex: 1;
          justify-content: center;
        }
      }
    }
  }
}

// 다크 테마 스타일
:host-context(.dark-theme) .user-detail-dialog {
  .dialog-header {
    background: linear-gradient(135deg, #00d4aa 0%, #00b894 100%);
  }
}
